<template>
  <div class="container">
    <div class="nav-head-title">
      <span class="p-span">
        <span>首页 </span> &nbsp;/&nbsp; 
        <span>订单 </span> &nbsp;/&nbsp; 
        <span class="p-span-active">生成新订单</span>
      </span>
    </div>
    <el-row>
      <el-col :span="24"><div class="grid-content bg-purple-dark" id="order_id">订单号：{{ order_id }}</div></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="7"><div class="grid-content bg-purple">下单时间：{{ create_time }}</div></el-col>
      <el-col :span="5"><div class="grid-content bg-purple">金额：{{ price }}</div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple">支付方式：COD</div></el-col>
      <el-col :span="6"><div class="grid-content bg-purple">订单状态：{{ status }}</div></el-col>
    </el-row>

    <el-form :model="form" :rules="rules" ref="form" label-width="100px" class="demo-ruleForm">
    <h2>联系人信息</h2>
      <el-form-item label="姓名" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="电话" prop="phone">
        <el-input v-model="form.phone"></el-input>
      </el-form-item>
      <el-form-item label="国家/地区" required>
        <el-select v-model="form.country" placeholder="请选择区域">
          <el-option label="马来西亚" value="MY"></el-option>
          <el-option label="新加坡" value="SG"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="即时配送" prop="delivery">
        <el-switch v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="活动性质" prop="type">
        <el-checkbox-group v-model="form.type">
          <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
          <el-checkbox label="地推活动" name="type"></el-checkbox>
          <el-checkbox label="线下主题活动" name="type"></el-checkbox>
          <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="特殊资源" prop="resource">
        <el-radio-group v-model="form.resource">
          <el-radio label="线上品牌商赞助"></el-radio>
          <el-radio label="线下场地免费"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="活动形式" prop="desc">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">立即创建</el-button>
        <el-button @click="resetForm('form')">重置</el-button>
      </el-form-item>
    </el-form>
    <table class="tb" style="width:100%;">
      <tr>
        <th>姓名</th>
        <td>{{ name }}</td>
        <th>电话</th>
        <td>{{ phone }}</td>
      </tr>
      <tr>
        <th>国家/地区</th>
        <td>{{ country }}</td>
        <th>省/市</th>
        <td>{{ province }}</td>
      </tr>
      <tr>
        <th>县</th>
        <td>{{ city }}</td>
        <th>区</th>
        <td>{{ area }}</td>
      </tr>
      <tr>
        <th>地址</th>
        <td>{{ address }}</td>
        <th>邮政编码</th>
        <td>{{ post_code }}</td>
      </tr>
      <tr>
        <th>备注</th>
        <td>{{ remark }}</td>
        <th>ip</th>
        <td>{{ ip }}</td>
      </tr>
    </table>

    <h2>订单信息</h2>
    <table class="tb" style="width:100%;">
      <tr>
        <th>sku</th>
        <th>标题</th>
        <th>数量</th>
        <th>单价</th>
      </tr>
      <tr v-for="orderGood in orderGoods">
        <td>{{ orderGood.sku_code }}</td>
        <td>{{ orderGood.pname }}</td>
        <td>{{ orderGood.number }}</td>
        <td>{{ orderGood.price }}</td>
      </tr>
    </table>

  </div>
</template>

<script>
import axios from 'axios'

export default {
  data(){
    return {
      order_id:"",
      create_time:"",
      price:"",
      status:"",
      name:"",
      phone:"",
      country:"",
      province:"",
      city:"",
      area:"",
      address:"",
      post_code:"",
      remark:"",
      ip:"",
      orderGoods:[],
      orderLogs:[],
      trackLogs:[],
      dialogFormVisible: false,
      form: {
        remark: '',
        type: [],
        track_number: '',
        status: [],
      },
      rules: {
        track_number: [
          { required: true, message: '请输入物流单号', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '请选择问题类型', trigger: 'change' }
        ],
        status: [
          { required: true, required: true, message: '请选择问题状态', trigger: 'change' }
        ],
        remark: [
          { required: true, required: true, message: '请输入备注', trigger: 'blur' }
        ],

      },

    }
  },
  methods: {

  },
  created() {
    var s = location.search;
    let that = this;
    axios({
      method:"GET",
      url:'http://192.168.10.141/v1/order/details'+s,
    }).then(function(re){
      console.log(re);
      var data = re.data.data;
      that.order_id = data.order.id;
      that.create_time = data.order.create_time;
      that.price = data.order.total_price;
      that.status = data.order.status;
      that.name = data.order.user_name;
      that.phone = data.order.phone;
      that.country = data.order.country;
      that.province = data.order.province;
      that.city = data.order.city;
      that.area = data.order.area;
      that.address = data.order.address;
      that.post_code = data.order.post_code;
      that.remark = data.order.remark;
      that.ip = data.order.user_ip;
      that.orderGoods = data.orderGoods;
      that.orderLogs = data.orderLogs;
      that.trackLogs = data.trackLogs;
    });

  }
}

</script>

<style>
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */
.container {
  padding: 30px 30px 0;
  overflow: auto;
}
.main-container{
    background-color:white;
    width:99%;   
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.for-holder-color .el-input__inner::-webkit-input-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-moz-placeholder {
  color: black;
}
.for-holder-color .el-input__inner:-ms-input-placeholder {
  color: black;
}
/* 设置table trhover 字体 */
tr:hover{
    color:#13c19f;
}
.input-search{
    width:260px;float:left;margin:11px;
}
.high-search{
    float:left;margin-top:10px;font-size:14px;line-height:32px;color:#aaa;
}
.select-c{
    color:black;width:120px;float:left;margin:10px;
}
.btn-add{
    float:right;margin:5px;background-color:#13c19f;color:white;border-color:#13c19f; 
}
.page-div{
    width:100%;height:10%;
}
.select-out{
    float:right;margin:5px;width:80px;
}
.grid-content{
  line-height:35px;
}
.el-row {
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
}
.tb{
  border-top:1px solid black;
  border-left:1px solid black;
}
.tb th,.tb td{
  border-right:1px solid black;
  border-bottom:1px solid black;
  height:40px;
}
.tb th,.tb td{
  text-align: center;
}

</style>



